	module.exports = {"data":{"markdownRemark":{"html":"<p>Per sostenere le nostre attività e i nostri progetti ci avvaliamo delle possibilità concesse per legge alle associazioni di promozione sociale quale è la nostra: tesseramenti, corsi, seminari, eventi a costo fisso o a donazione, altre libere donazioni, crowdfunding e il 5 per mille.<br>\nIl nostro lavoro è a titolo volontario.<br>\nIl denaro serve a coprire le molte spese associative, gli affitti delle sale, la stampa dei volantini, l'acquisto del materiale, i rimborsi spese, ma soprattutto: ogni guadagno viene reinvestito nei nostri nuovi progetti, di cui quello più ambizioso è l'apertura e il mantenimento di un Tempio della Grande Madre locale, aperto regolarmente.</p>\n<br/>\n<p><strong>Vuoi aiutarci?</strong></p>\n<p>Condividi le nostre visioni? Ti piacciono i nostri progetti? Ti piacerebbe che ci fosse un tempio a disposizione di tutte/i anche nella nostra area?<br>\nAnche piccolissime donazioni ci aiutano, perché tante piccole donazioni possono farne una grossa!  </p>\n<p>Puoi anche devolvere il tuo 5 per Mille dalla dichiarazione dei redditi (a te non costa nulla ma per noi vale tantissimo!), basta indicare il nostro codice fiscale nell'apposito campo del modello 730: 90017350225.<br>\nOppure diventare socio/a e partecipare ai nostri eventi.</p>\n<p>Ma puoi aiutare anche mettendo a disposizione dell'associazione i tuoi talenti!<br>\nIn futuro potremmo aver bisogno di manutentori, elettricisti, muratori, carpentieri e altre figure.</p>\n<p>Se invece hai una vecchia sala, cantina, stanza grande o qualsiasi locale inutilizzato dotato di minimi standard igienici, forse ti piacerebbe dargli un senso e \"prestarcelo\" (comodato d'uso o affitto simbolico): sappi che stiamo cercando uno spazio da adibire a \"Tempio della Grande Madre\", sede della nostra associazione possibilmente non troppo lontana dalla città/paesi limitrofi. Anche a tempo determinato!<br>\nCe ne prenderemo cura, lo addobberemo, lo terremo pulito, profumato e lo  renderemo \"spazio sacro\".  </p>\n<br/>\n<form action=\"https://www.paypal.com/cgi-bin/webscr\" method=\"post\" target=\"_blank_\">\n<input type=\"hidden\" name=\"cmd\" value=\"_s-xclick\">\n<input type=\"hidden\" name=\"hosted_button_id\" value=\"XX9ES4XG3K54C\">\n<input type=\"image\" src=\"https://www.paypalobjects.com/it_IT/IT/i/btn/btn_donateCC_LG.gif\" border=\"0\" name=\"submit\"_>\n<img alt=\"\" border=\"0\" src=\"https://www.paypalobjects.com/it_IT/i/scr/pixel.gif\" width=\"1\" height=\"1\">\n</form>\n<br/>\n<p>TI RINGRAZIAMO CON TUTTO IL NOSTRO CUORE!</p>\n<br/>\n<p>Per saperne di più e capire meglio la nostra posizione puoi leggere l'articolo <a href=\"/spiritualit%C3%A0-e-denaro\"><strong>\"Spiritualità e denaro\"</strong></a></p>","frontmatter":{"path":"/donazioni","title":"Donazioni e raccolta fondi"}}},"pathContext":{}}

